import React from 'react'
import Link from '@material-ui/core/Link'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import Typography from '@material-ui/core/Typography'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import CloseButton from '../utils/CloseButton'
import AboutMeContent from './AboutMeContent'

interface IProps {
  open: boolean
  handleClose: () => void
}

const email = ['k.mikolajczuk123', 'gmail.com'].join('@')

export default function AboutMeDialog({ open, handleClose }: IProps) {
  return (
    <Dialog onClose={handleClose} open={open} maxWidth='md'>
      <DialogTitle disableTypography>
        <Typography variant='h6'>O mnie a waciwie o tym projekcie</Typography>

        <CloseButton onClick={handleClose} />
      </DialogTitle>

      <DialogContent dividers>
        <AboutMeContent />

        <Typography variant='h6' gutterBottom>
          Kontakt
        </Typography>

        <Typography gutterBottom>
          <Link href='https://github.com/mikolajczukkamil'>M贸j Github</Link>
        </Typography>

        <Typography gutterBottom>
          <Link href='https://www.linkedin.com/in/kamil-miko%C5%82ajczuk-61682b16a/'>
            M贸j LinkedIn
          </Link>
        </Typography>

        <Typography gutterBottom>
          <Link href={`mailto:${email}`}>E-mail: {email}</Link>
        </Typography>
      </DialogContent>

      <DialogActions>
        <Button color='secondary' variant='contained' onClick={handleClose}>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  )
}
