import React from 'react'
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Link from '@material-ui/core/Link'
import Toolbar from '@material-ui/core/Toolbar'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'

import GithubIcon from '@material-ui/icons/GitHub'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexDirection: 'column',
      marginTop: theme.spacing(4)
    },
    title: {
      flex: 1
    },
    top: {
      [theme.breakpoints.down('xs')]: {
        textAlign: 'center'
      },
      paddingTop: theme.spacing(4),
      paddingBottom: theme.spacing(4),
      color: theme.palette.primary.contrastText,
      backgroundColor: theme.palette.primary.main
    },
    bottom: {
      userSelect: 'none',
      color: theme.palette.primary.contrastText,
      backgroundColor: theme.palette.primary.dark
    },
    iconLink: {
      [theme.breakpoints.down('xs')]: {
        marginTop: theme.spacing(4)
      },
      margin: 'auto',
      display: 'block',
      fontSize: theme.spacing(12)
    },
    icon: {
      display: 'block'
    },
    flex: {
      display: 'flex'
    }
  })
)

const year = new Date().getFullYear().toString()
const email = ['k.mikolajczuk123', 'gmail.com'].join('@')

export default function Footer() {
  const classes = useStyles()

  return (
    <footer className={classes.root}>
      <div className={classes.top}>
        <Grid container component={Container} maxWidth='md'>
          <Grid item xs={12} sm={6}>
            <Typography gutterBottom variant='h6'>
              Linki
            </Typography>
            <Typography gutterBottom>
              <Link
                href='https://github.com/mikolajczukkamil'
                color='inherit'
                title='Publiczny GitHub'
              >
                M贸j Github
              </Link>
            </Typography>

            <Typography gutterBottom>
              <Link
                href='https://www.linkedin.com/in/kamil-miko%C5%82ajczuk-61682b16a/'
                color='inherit'
              >
                M贸j LinkedIn
              </Link>
            </Typography>

            <Typography gutterBottom>
              <Link href={`mailto:${email}`} color='inherit'>
                E-mail: {email}
              </Link>
            </Typography>
          </Grid>
          <Grid item xs={12} sm={6} classes={{ root: classes.flex }}>
            <Link
              color='inherit'
              title='GitHub projekt贸w uczelnianych'
              target='_blank'
              classes={{ root: classes.iconLink }}
              href='https://github.com/KamilMikolajczukStudia/'
            >
              <GithubIcon fontSize='inherit' classes={{ root: classes.icon }} />
            </Link>
          </Grid>
        </Grid>
      </div>

      <div className={classes.bottom}>
        <Toolbar>
          <Typography classes={{ root: classes.title }} align='center'>
            &copy; {year} Copyright: Kamil Mikoajczuk
          </Typography>
        </Toolbar>
      </div>
    </footer>
  )
}
